export class Role {
  RoleID: number;
  Name: string;
  RemoteAccess: boolean;
  constructor(RoleID: number, Name: string, RemoteAccess: boolean) {
    this.RoleID = RoleID;
    this.Name = Name;
    this.RemoteAccess = RemoteAccess;
  }
}

export class RoleMessage {
  Count: number;
  Role: Role;
}

export class RolesMessage {
  Count: number;
  Roles: Role[];
}
