import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { MatSnackBar } from '@angular/material';
import { PreferenceService } from "../preference.service";
import { Preference, PreferencesMessage } from "../preference";

@Component({
  selector: 'dmz-preference-list',
  templateUrl: './preference-list.component.html',
  styleUrls: ['./preference-list.component.css']
})
export class PreferenceListComponent implements OnInit {

  Preferences: Preference[] = [];
  tableColumns: string[] = ['PreferenceID', 'Name', 'Value'];
  selectedRow = 0;
  selectedRowNumber = 0;

  constructor(private PreferenceService: PreferenceService,
    private snackBar: MatSnackBar,
    private location: Location) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

ngOnInit() {
    this.PreferenceService.getPreferences().subscribe((PreferencesMsg: PreferencesMessage) => {
      this.Preferences = PreferencesMsg.Preferences;
      console.log(this.Preferences);
      this.snackBar.open(PreferencesMsg.Count + ' Preference' + (this.Preferences.length > 1 ? 's' : '') + ' loaded. ', '', { duration: 3000 });
      return this.Preferences;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Preferences found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }
}
