import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { MatSnackBar } from '@angular/material';
import { InterfaceService } from "../interface.service";
import { Interface, InterfacesMessage } from "../interface";

@Component({
  selector: 'dmz-interface-list',
  templateUrl: './interface-list.component.html',
  styleUrls: ['./interface-list.component.css']
})
export class InterfaceListComponent implements OnInit {

  Interfaces: Interface[] = [];
  tableColumns: string[] = ['InterfaceID', 'Name', 'Parameters', 'Configuration', 'Notifiable', 'Active'];
  selectedRow = 0;
  selectedRowNumber = 0;

  constructor(private interfaceService: InterfaceService,
    private snackBar: MatSnackBar,
    private location: Location) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.interfaceService.getInterfaces().subscribe((InterfacesMsg: InterfacesMessage) => {
      this.Interfaces = InterfacesMsg.Interfaces;
      this.snackBar.open(InterfacesMsg.Count + ' Interface' + (this.Interfaces.length > 1 ? 's' : '') + ' loaded. ', '', { duration: 3000 });
      console.log(this.Interfaces);
      return this.Interfaces;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Interfaces found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }
}
