import { Component, OnInit } from '@angular/core';
import { PreferenceService } from "../preference.service";
import { Preference } from "../preference";
import { PreferenceMessage } from "../preference";

@Component({
  selector: 'dmz-preference',
  templateUrl: './preference.component.html',
  styleUrls: ['./preference.component.css']
})
export class PreferenceComponent implements OnInit {
  Preference: Preference = new Preference(-1, 'No Preference', "No Value");
  constructor(private PreferenceService: PreferenceService) { }
  ngOnInit() {
    this.PreferenceService.getPreference(1).subscribe((PreferenceMsg: PreferenceMessage) => {
      this.Preference = PreferenceMsg.Preference;
    })
  }
}
