import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { UserService } from "../user.service";
import { User, UsersMessage } from "../user";

@Component({
  selector: 'dmz-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {

  Users: User[] = [];
  tableColumns: string[] = ['UserID', 'UserName', 'Password', 'FirstName', 'LastName', 
    'RoleID', 'Active', 'ForceChange', 'FailedAttempts', 'Theme', 'Timestamp'];
  selectedRow = 0;
  selectedRowNumber = 0;

  constructor(private userService: UserService,
    private snackBar: MatSnackBar,
    private location: Location,
    private route: ActivatedRoute) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
      this.userService.getUsers(this.route.snapshot.params['id']).subscribe((UsersMsg: UsersMessage) => {
      this.Users = UsersMsg.Users;
      this.snackBar.open(UsersMsg.Count + ' User' + (this.Users.length > 1 ? 's' : '') + ' loaded. ', '', { duration: 3000 });
      console.log(this.Users);
      return this.Users;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Users found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }
}
